import React, {Component} from 'react';
import { Card,Dropdown } from 'react-bootstrap';
import Trivia from './Trivia';
import Menu from './Menu';

//Game acts as a store for options / game state
class Game extends Component {
    constructor(props) {
        super(props);
        this.state = {
          category: ""
        };
        this.handleCategoryChange = this.handleCategoryChange.bind(this);

      }

    handleCategoryChange = (e) => {
        this.setState({category: e.target.value});
    };

    render() {
        return (
            <>
            <Trivia category={this.state.category} handleCategoryChange={this.handleCategoryChange.bind(this)} />
            <Menu category={this.state.category} handleCategoryChange={this.handleCategoryChange.bind(this)} />
            </>
        );
    }
}
   
export default Game;